				this.OpenBluetoothAdapter()
			},
				  /** * 第一步 在页面显示的时候判断是否已经初始化完成蓝牙适配器若成功，则开始查找设备   */
			OpenBluetoothAdapter() {
				// debugger;
				uni.openBluetoothAdapter({
				success:(res) => {
				console.log('第一步初始化蓝牙成功:' + res.errMsg);
				// 初始化完毕开始搜索
					this.StartBluetoothDeviceDiscovery()
				},
				fail:(res) => {
				    console.log('初始化蓝牙失败: '+JSON.stringify(res));
				    if (res.errCode == 10001) {
				        uni.showToast({
				            title: '蓝牙未打开',
				            duration: 2000,
				        })
				    } else {
				        uni.showToast({
				            title: res.errMsg,
							duration: 2000,
				        })
				      }
				    }
				});
			},
	 /** 第二步 在页面显示的时候判断是都已经初始化完成蓝牙适配器若成功，则开始查找设备   */
			StartBluetoothDeviceDiscovery() {
				uni.startBluetoothDevicesDiscovery({
				    // services: ['0000FFE0'],
				    success: res => {
				        console.log('第二步开始搜寻附近的蓝牙外围设备：startBluetoothDevicesDiscovery', res)
						uni.onBluetoothDeviceFound(res => {
						    console.log("监听寻找到新设备的事件:",JSON.stringify(res.devices))
						    console.log("第三步 监听寻找到新设备列表:",res.devices)
						    res.devices.forEach(device => {//这一步就是去筛选找到的蓝牙中,有没有你匹配的名称  
						        console.log("这一步就是去筛选找到的蓝牙中,有没有你匹配的名称:",JSON.stringify(device))
						        if (device.name == "Printer001") {//匹配蓝牙名称
						            uni.setStorageSync("DeviceID",device.deviceId)
								//把已连接的蓝牙设备信息放入缓存
									console.log(device.deviceId,"***********************************");
						            this.DeviceID = device.deviceId
						            let DeviceID = device.deviceId//这里是拿到的uuid 
						            this.StopBluetoothDevicesDiscovery()
								//当找到匹配的蓝牙后就关掉蓝牙搜寻,因为蓝牙搜寻很耗性能 
						            console.log("匹配到的蓝牙this.DeviceID：",this.DeviceID) 
						        }
							})
						});
						console.log("********************************");
				    },
				    fail: res => {
				        uni.showToast({
				            icon: "none",
				            title: "查找设备失败！",
				            duration: 3000
				        })
				    }
				});
			},	
			 /**    * 第四步 停止搜索蓝牙设备  */
			StopBluetoothDevicesDiscovery() {
			    uni.stopBluetoothDevicesDiscovery({
			        success: res => {
			            console.log("第四步 找到匹配的蓝牙后就关掉蓝牙搜寻:",JSON.stringify(res))
						var id = uni.getStorageSync('DeviceID')
						console.log(id,"00");
						this.CreateBLEConnection(id);				 
			        },
			        fail: res => {
			            console.log('第四步 停止搜索蓝牙设备失败，错误码：' + res.errCode);
			        }
			    });
			},
