			CreateBLEConnection(id){
				let doc = this
				const deviceId = id;
				console.log(deviceId,"++++++++++++++++");
				uni.createBLEConnection({//创建蓝牙连接,连接低功耗蓝牙设备  
				deviceId,//传入刚刚获取的uuid  
				success(res) {
					console.log("第五步 创建蓝牙连接成功:",JSON.stringify(res))
					console.log(deviceId,"5456464646");
					doc.GetBLEDeviceServices(id) //获取蓝牙设备所有服务(service)。
				},
				fail(res) {
					console.log(res)
				}
				})
			},
