					let doc = this
			        console.log("第七步获取蓝牙特征值id:",id,"serviceId:",uni.getStorageSync('ServiceUUIDNew'));
					setTimeout(()=>{
			        uni.getBLEDeviceCharacteristics({//获取蓝牙设备某个服务中所有特征值  
			            deviceId: id,
			            serviceId: uni.getStorageSync('ServiceUUIDNew'),
										//这个serviceId可以在上一步获取中拿到,也可以在  
			 //蓝牙文档中(硬件的蓝牙文档)拿到,我这里是通过文档直接赋值上去的,一般有两个,一个是收的uuid,一个是发的uuid,我们这边是发  
						success(res) {
			                console.log("第七步获取蓝牙设备某个服务中所有特征值成功:",JSON.stringify(res))
							console.log('getBLEDeviceCharacteristics success', res.characteristics)
							for (let i = 0; i < res.characteristics.length; i++) {
							          let item = res.characteristics[i]
									  console.log(item,"00000");
							}
			                uni.setStorageSync("CharacteristicId",res.characteristics[1].uuid)
								//把某个服务中所有特征值信息放入缓存
			                this.characteristicId = res.characteristics[1].uuid
								console.log("4444",this.characteristicId);
								doc.WriteBLECharacteristicValue()
			            },
			            fail(res) {
			                console.log("获取蓝牙设备某个服务中所有特征值失败:",JSON.stringify(res))
			             }
			         })
			    },2000)
			},
			
